import React, { useEffect, useCallback, useState, useRef } from "react";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import queryString from "query-string";

import "./styles.scss";

const LazyLoadImageWithDimensions = (props) => {
    const ref = useRef(null);

    const placeholderSrc = (img) => {
        let { width, height } = img;
        return `data:image/svg+xml,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${width} ${height}"%3E%3C/svg%3E`;
    };

    const [state, setState] = useState({
        isExpanded: false,
        loaded: false,
        src: placeholderSrc(props.img),
        shouldFetch: true
    });

    const removeListeners = () => {
        window.removeEventListener("scroll", handleScroll);
    };

    const loadImage = useCallback (() => {
        setState({
            ...state,
            src: props.img.url,
            shouldFetch: false,
        });
        removeListeners();
    }, [props.img.url, state]);

    const shouldLoadImage = useCallback((componentOffsetTop, userYPosition) => {
        return state.shouldFetch && !state.loaded && componentOffsetTop <= userYPosition + document.body.clientHeight + 100; 
    }, [state.loaded, state.shouldFetch]);

    const onImageLoad = () => {
        if (!state.loaded && state.src === props.img.url) {
            setState({
                ...state,
                loaded: true
            });
        }
    };

    const onError = () => {
        setState({
            ...state,
            loaded: true
        });
        removeListeners();
    };

    const handleScroll = useCallback(() => {
        const yPosition = window.scrollY;
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
        }
    }, [loadImage, shouldLoadImage]);

    const { url, alt, hiResUrl, isNotExpandable } = props.img;

    const onClick = () => {
        if (!isNotExpandable) {
            setState({
                ...state,
                isExpanded: true
            });
        }
    };
    const onClose = () => {
        setState({
            ...state,
            isExpanded: false
        });
    }

    useEffect(() => {
        const yPosition = window.scrollY;
        // Handles initial mount to check if image is already within threshold without scrolling.
        if (shouldLoadImage(ref.current.offsetTop, yPosition)) {
            loadImage();
        } else {
            window.addEventListener('scroll', handleScroll);
        }

        return () => {
            removeListeners();
        };
    }, [shouldLoadImage, loadImage, handleScroll, state]);

    const { loaded, src } = state;
    let classes = "";
    if (props.className) {
        classes += " " + props.className;
    }
    classes += " " + (!loaded ? "LazyLoadAnimation" : "");

    classes = classes.trim();

    let expandedUrl = url;
    if (hiResUrl) {
        expandedUrl = url.substring(0, url.lastIndexOf("/")) + "/" + hiResUrl; 
    }

    return (
        <>
            <img ref={ref} onLoad={onImageLoad} onError={onError} onClick={onClick} className={classes} src={src} data-src={url} alt={alt} loading="lazy" />

            <noscript>
                <img src={url} alt={alt} loading="lazy" />
            </noscript>
        </>
    );
}

const Img = (props) => {
    const { src } = props;
    const sepIdx = src.indexOf("?");

    if (sepIdx >= 0) {
        const params = queryString.parse(src.substring(sepIdx));
        if (params.height && params.width) {
            const img = {
                url: src,
                height: params.height,
                width: params.width,
                hiResUrl: params.hiRes,
                alt: props.alt,
                isNotExpandable: params.isNotExpandable,
            }
            return (
                <div className="LazyLoadImageWrapper">
                    <LazyLoadImageWithDimensions img={img} {...props} />
                </div>
            );
        }
    }

    return (
        <div className="LazyLoadImageWrapper">
            <LazyLoadImage {...props} effect="blur" threshold={300} />
        </div>
    );
};

export default Img; 