import React from 'react';
import { Link } from "gatsby";
import favicon from '../../images/tw.svg';
import rssicon from '../../images/rss.svg';

import OutBoundLink from "../Link";

import './_header.scss';

const Header = () => (
    <header className="header">
        <nav>
            <ul className="header-nav-list">
                <li>
                    <Link to={`/`}><img src={favicon} alt="TechWaifu" /> </Link>
                </li>
                <li className="NavItem">
                    <Link to={`/`}>Home </Link>
                </li>
                <li className="NavItem">
                    <Link to={`/store/merch`}>Merch </Link>
                </li>
            </ul>

            <ul className="header-nav-list right-side">
                <li className="NavItem">
                    <OutBoundLink
                        href={`/rss.xml`}
                        stayOnPage={true}
                        title="RSS Feed">
                        <img className="RSSIcon" src={rssicon} alt="RSS Icon" />
                    </OutBoundLink>
                </li>
            </ul>
        </nav>
    </header>
);

export default Header;
